// SmoothScrollComponent.js
import React, { useEffect } from 'react';

const SmoothScrollComponent = () => {
  useEffect(() => {
    const handleScroll = event => {
      const scrollPosition = window.scrollY;

      if (event.deltaY > 0 && scrollPosition === 0) {
        event.preventDefault(); //   
        //    
        window.scrollTo({
          top: window.innerHeight, // 100vh  
          behavior: 'smooth', //  
        });
      } else if (event.deltaY < 0 && scrollPosition <= window.innerHeight) {
        //    
        event.preventDefault(); //   
        window.scrollTo({
          top: 0, //  
          behavior: 'smooth', //  
        });
      }
    };

    window.addEventListener('wheel', handleScroll, { passive: false });

    return () => {
      window.removeEventListener('wheel', handleScroll);
    };
  }, []);

  return null; //     
};

export default SmoothScrollComponent;
